@import url(https://fonts.googleapis.com/css?family=Fredoka+One);
.App-logo {
    height: 130px;
    -webkit-filter: drop-shadow(0 0 0.30rem #f8ff9c);
            filter: drop-shadow(0 0 0.30rem #f8ff9c);
    align-items: center;
    justify-content: center;
    text-align: center;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}

.App-logo:hover { -webkit-transform: scale(1.2); transform: scale(1.2); }

.App-logo-rotation {
    -webkit-animation: App-logo-spin infinite 5s linear;
            animation: App-logo-spin infinite 5s linear;
  }
  
@-webkit-keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
@keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
.button {
    font-size: 16px;
    font-weight: lighter;
    height: 50px;
    border-radius: 40px;
    width: 100%;
    margin: 5px 0px 5px 0px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    box-shadow:0 0 1px #111110, 0 0 10px rgb(193, 194, 190);
}

.box {
    padding: 10px;
    background-color: white;
    border-radius: 10px;
    box-shadow:0 0 1px #111010, 0 0 10px rgb(193, 194, 190);
}

.button:hover { 
    -webkit-transform: scale(1.01); 
            transform: scale(1.01); 
}

.button:focus { 
    -webkit-transform: scale(1.01); 
            transform: scale(1.01); 
    box-shadow:0 0 5px #111010, 0 0 10px rgb(193, 194, 190);
}
body {
  background-color: #f04d3b; /* e74c3c */
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-family: 'Fredoka One', cursive;
  color: white;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.title {
  font-weight: bold;
}

.App {
  text-align: center;
  color: #131313;
}

.App-header {
  background-color: #c94233; /*c0392b*/
  display: flex;
  min-height: 5vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(25px + 2vmin);
  color: white;  
  text-shadow: 0 0 1px #2b0f0f, 0 0 2px rgb(193, 194, 190);
  margin-bottom: 20px;
  box-shadow: 0 0 2px #cf4242, 0 0 6px #2B2D1F;

}

a {
  color: #f1fb61;
}

a:hover {
  color: #fddec0;
}

.footer {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 15px;
  color: #ffffff;
  text-shadow: 0 0 2px #cf4242, 0 0 3px rgb(193, 194, 190);
}


.timer {
  font-size: 100px; 
  font-weight: normal;
  text-shadow: 0 0 1px #4b4b4b, 0 0 5px rgb(193, 194, 190);
  color: white;
}
